/* eslint-disable */
// @ts-ignore
import Vue from 'vue';
import VueRouter, { RouterOptions, RouteConfig } from 'vue-router';

Vue.use(VueRouter);

const routes = <Array<RouteConfig>>[
  {
    path: '/',
    component: () => import(/* webpackChunkName: "navless-container" */ '@/containers/NavlessContainer.vue'),
    children: [
      {
        path: '/',
        name: 'landing',
        component: () => import(/* webpackChunkName: "landing" */ '@/views/Landing'),
      },
    ],
  },
  {
    path: '/page',
    name: 'page',
    component: () => import(/* webpackChunkName: "nav-container" */ '@/containers/NavContainer.vue'),
    children: [
      {
        path: '/home',
        name: 'home',
        component: () => import(/* webpackChunkName: "home" */ '@/views/Home'),
      },
      {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '@/views/About'),
      },
      {
        path: '/experiments',
        name: 'experiments',
        component: () => import(/* webpackChunkName: "experiments" */ '@/views/Experiments'),
      },
      {
        path: '/experiment/:experiment',
        name: 'experiment',
        component: () => import(/* webpackChunkName: "experiment" */ '@/views/Experiment'),
        props: true,
      },
    ],
  },
];

const routerOptions = <RouterOptions>{
  routes,
}

const router = new VueRouter(routerOptions);

export default router;
